import React, { useState, useEffect } from 'react';
import { Button } from './Button';
import { Link } from 'react-router-dom';
import './Navbar.css';
import { Controls, PlayState, Tween } from 'react-gsap';
import logo from './images/NSUt.png'


function Navbar() {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener('resize', showButton);

  return (
    <>
      <nav className='navbar'>
        <div className='navbar-container'>
          <Link to='/' className='navbar-logo' onClick={closeMobileMenu}>
           <span className="l" style={{animationDelay:"150ms"}}>bN</span ><span className="l" style={{animationDelay:"300ms"}}>S</span><span className="l" style={{animationDelay:"450ms"}}>U</span><span className="l" style={{animationDelay:"600ms"}}>T</span>
            <img className="logo l" src={logo}/>
          </Link>
          <div className='menu-icon' onClick={handleClick}>
            <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
          </div>
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
         
 
            <li className='nav-item'>
              <Link to='/' className='nav-links' onClick={closeMobileMenu}>
              <span className="nav-sp">Home</span>  
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Blogs
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Our Alumni
              </Link>
            </li>

            <li className='nav-item'>
              <Link
                to='/-up'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Login/Register
              </Link>
            </li>
 

          </ul>
        </div>
      </nav>
    </>
  );
}

export default Navbar;
