import React from 'react'
import { Controls, PlayState, Timeline, Tween } from 'react-gsap';
import './starter.css'

function starter() {
    return (
        
        <div className="cont" style={{height:"100vh",backgroundColor:"black",color:"white",width:"100%"}}>
      
            <Tween  to={{ x: '40%' }} stagger={0.5} ease="elastic.out(0.2, 0.1)">
                <div className="intro" style={{animationDelay:"100ms"}}>Say Hi!</div>
                <div className="intro" style={{animationDelay:"300ms"}}>To The Newest</div>
                <div className="intro" style={{animationDelay:"500ms"}}>And The Fastest</div>
                <div className="intro" style={{animationDelay:"700ms"}}>Growing BlogNation</div>
                </Tween>   

            </div>
    
    )
}

export default starter
