import React from 'react';
import './Cards.css';
import CardItem from './CardItem';
import p1 from './images/CONCERT.jpg'
import p2 from './images/project.jpg'
import p3 from './images/amazon.png'
import p4 from './images/coding.jpg'
import p5 from './images/gta.jpg'


function Cards() {
  return (
    <div className='cards'>
      <h1>Check Out the Trending Articles!</h1>
      <div className='cards__container'>
        <div className='cards__wrapper'>

          <ul className='cards__items'>
          <CardItem
              src={p1}
              text='INNO-RESO Fest'
              label='College Life'
              path='/blog:1(id)'
              author='Harmeet Singh'
              color='blue'
            />
            <CardItem
              src={p2}
              text='What Projects to Do in 2nd Year?'
              label='Technical Topics'
              path='/blog:2(id)'
              author={'Rahul Sharma'}
              color='red'
            />
            <CardItem
              src={p3}
              text='How to Clear Amazon Selection Process'
              label='ALUMNI Interview'
              path='/blog:3(id)'
              author='Aryan Singh'
              color='yellow'
            />
            <CardItem
              src={p4}
              text='Confused As Every NSUTian as to where to start coding from?'
              label='Technical Topics'
              path='/blog:4(id)'
              author='Meghna Walia'
              color='red'
            />
            <CardItem
              src={p5}
              text='Gaming Sessions In Lectures'
              label='College Life'
              path='/blog:5(id)'
              author='Nikhilesh Shah'
              color='blue'
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
